import * as React from "react";
import Layout from '../components/layout';
import InnerPageTitle from '../components/utils/inner-page-title';
import CShirtsPreview from '../components/cshirts/cshirtPreview';
import Constants from "../utils/constants";
import {Link} from 'gatsby';

const CShirtTemplate = ({ pageContext } : { pageContext:any }) => {
    const shirt = pageContext.shirt;
    const titleNumber = (shirt.Number) ? 'n. ' + shirt.Number : '';
    var title = '';
    if(shirt.Type == "Player") {
        title = (shirt.Captain == 'Yes') ? 'Player ' + titleNumber + '(C)' : 'Player ' + titleNumber;
    }
    else {
        title = shirt.Role
        .replace('AssistantCoach', 'Assistant Coach')
        .replace('GeneralManager', 'General Manager')
        .replace('HeadCoach', 'Head Coach') + ' (staff)';
        if(shirt.Role == "President") {
            title = shirt.Role;
        }
    };
   
  return (
    <Layout pageTitle={"Non Fungible Cup: C-Shirts #" + shirt.ID} container="inner-page">
        <InnerPageTitle title={pageContext.collectionName + " #" + shirt.ID} subtitle="NFT details" />
        <section className="tf-section tf-item-details pb-mobie">
            <div className="container">
                <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12">
                        <div className="image-details">
                            <img src={pageContext.imgPath + '/336x336/' + shirt.ID + ".png"} alt={pageContext.collectionName + ' #' + shirt.ID} />
                        </div>
                        <div className={shirt.ID < Constants.TOTAL_MINTED ? "image-details" : "hide"}>
                            <a href={pageContext.openSeaBasePath + shirt.ID} className="tf-button btn-effect active" target="_blank">
                                <span className="boder-fade"></span>          
                                <span className="effect">View On Opensea</span>
                            </a>  
                        </div>
                        <div className={shirt.ID >= Constants.TOTAL_MINTED ? "image-details" : "hide"}>
                            <Link to="/mint" className="tf-button btn-effect active">
                                <span className="boder-fade"></span>          
                                <span className="effect">Mint</span>
                            </Link>  
                        </div>
                    </div>
                    <div className="col-xl-6 col-lg-6 col-md-12">
                        <div className="item-details">
                            <h5>{title}</h5>
                            <p className="desc">{shirt.Description}</p>                            
                            <div className="list-product">
                                <div className="box corner-box">
                                    <p>Type</p>
                                    <h6 className="h7">{shirt.Type}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Sport</p>
                                    <h6 className="h7">{shirt.Sport}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Team</p>
                                    <h6 className="h7">{shirt.Team}</h6>
                                </div>
                                <div className={shirt.Number ? "box corner-box" : "hide"}>
                                    <p>Number</p>
                                    <h6 className="h7">{shirt.Number}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Role</p>
                                    <h6 className="h7">{shirt.Role}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Captain</p>
                                    <h6 className="h7">{shirt.Captain}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>CShirt</p>
                                    <h6 className="h7">{shirt.CShirt}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Background</p>
                                    <h6 className="h7">{shirt.Background}</h6>
                                </div>
                                <div className="box corner-box">
                                    <p>Primary color</p>
                                    <h6 className="h7">{shirt.PrimaryColor}</h6>
                                </div>
                                <div className={shirt.SecondaryColor ? "box corner-box" : "hide"}>
                                    <p>Secondary color</p>
                                    <h6 className="h7">{shirt.SecondaryColor}</h6>
                                </div>
                                <div className={shirt.TertiaryColor ? "box corner-box" : "hide"}>
                                    <p>Tertiary color</p>
                                    <h6 className="h7">{shirt.TertiaryColor}</h6>
                                </div>
                                <div className={shirt.NumberColor && shirt.Type == "Player" ? "box corner-box" : "hide"}>
                                    <p>Number color</p>
                                    <h6 className="h7">{shirt.NumberColor}</h6>
                                </div>
                            </div>         
                        </div>
                    </div>
                </div>
            </div>
        </section>

        <section className="tf-section tf-item tf-teamplayers">
            <div className="container">
                <div className="row">
                    <div className="col-md-12">
                        <h4>Meet the team</h4>
                        <div className="wrap-fx">
                            {pageContext.team.map((teamShirt:any) => (
                                <div className="fl-item" key={teamShirt.ID}>
                                    <CShirtsPreview id={teamShirt.ID} url={pageContext.baseUrl} imgpath={pageContext.imgPath + '/72x72/'} collection={pageContext.collectionName} />
                                </div>
                            ))} 
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </Layout>
  )
}

export default CShirtTemplate